import React, { Component } from 'react'
import Navbar from './Navbar'
import Footer from './Footer'
import Items from './Items'
import Data from '../data.json'
import Articles from './Articles';
import Projects from './Projects'
export default class Home extends Component {
   constructor(){
      super();
      this.state = {
        educations:Data.data.educations,
        projects:Data.data.projects,
        articles:Data.data.articles
      }
   }
   
   componentDidMount(){
     console.log(this.state.educations);
   }

  render() {

    this.getHeight = (event) =>{
        console.log(event.target.offsetHeight);
    }

    return (
     <div>
          <Navbar />
          <div className="row">
                <div className="col-12 cover">
                </div>
                <div className="col-12">
                   <center>
                     <img className="rounded-circle profile" src="https://raw.githubusercontent.com/whoamishubham/whoamishubham.github.io/data/images/profile.jpg" alt="image" onChange={this.getHeight}/>
                     <figcaption> 
                          <span className="name">Shubham Jha</span>
                          <span className="position">UIT Undergraduate</span>
                          <span>
                            <a href="https://linkedin.com/in/whoamishubhamjha" className="fa fa-linkedin"></a>&nbsp;&nbsp;
                            <a href="https://github.com/whoami-shubham" className="fa fa-github"></a>&nbsp;&nbsp;
                            <a href="https://twitter.com/whoami_shubham" className="fa fa-twitter"></a>&nbsp;&nbsp;
                            <a href="https://www.facebook.com/whoami.shubham" className="fa fa-facebook"></a>
                          </span>
                     </figcaption>
                   </center>
                </div>
          </div>
          <div className="container my-5">
              <span className="name items_margin">Education</span>
              <hr/>
              <Items data={this.state.educations} />
              <span className="name items_margin">Recent projects</span>
              <hr/>
              <Projects data={this.state.projects} />
              <span className="name items_margin">Articles from my Blog</span>
              <hr/>
              <Articles data={this.state.articles} />
          </div>
          <div className="container items_margin">

          </div>
          <Footer />
    </div>
    )
  }
}
